@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Open Sans",sans-serif;margin:0;padding:0;box-sizing:border-box}.App{display:grid;grid-template-areas:"h h h" "n c d";grid-template-rows:64px 1fr;grid-template-columns:.5fr 10fr 4fr}.header{grid-area:h;padding-right:16px;padding-left:16px;display:flex;justify-content:space-between;align-items:center;box-shadow:0px 5px 5px #d3d3d3}.header_logoWrapper{display:flex;width:105px;justify-content:space-between}.header_logo{display:flex;flex-direction:column}.header_logoTitle{font-weight:bold;font-size:6px;line-height:7px;color:#000}.header_title{font-weight:bold;font-size:16px;line-height:30px;color:#000}.header_profileInfo{display:flex;justify-content:space-between;align-items:center;width:170px}.header_profileName{font-weight:normal;font-size:15px;line-height:19px}.navbar{grid-area:n;background:#550dc9;box-shadow:2px 0px 4px rgba(0,0,0,.1);padding-top:24px}.navbar_logoWrapper{height:32px;display:flex;justify-content:center;align-items:center;background:#440ba0}.todolist{grid-area:c;padding-top:24px;padding-left:24px}.todolist_totalTasks{display:flex;justify-content:center;align-items:center;margin-top:8px;width:54px;height:24px;background:#fef6ff;border-radius:4px;font-weight:500;font-size:12px;line-height:16px;color:#550dc9}.todolist_tasks{margin-top:20px}.todolist_title{font-weight:600;font-size:16px;line-height:22px;color:#373737;margin-bottom:8px}.completedTasks{grid-area:d;padding-top:24px}.completedTasks_title{font-weight:600;font-size:16px;line-height:22px;color:#373737;margin-bottom:8px}.addItemForm{margin-bottom:8px}.addItemForm_input{width:60vw;height:40px;background:#fff;border:1px solid #ececec;border-radius:4px;padding:12px 10px}.addItemForm_inputError{background:#bc8f8f;border:1px solid red}.addItemForm_btn{width:50px;height:40px;margin-left:8px;background:#550dc9;border-radius:4px;border:1px solid #550dc9;font-weight:normal;font-size:14px;line-height:19px;color:#fff;cursor:pointer}.task,.completedTask{width:64vw;display:flex;justify-content:space-between;align-items:center;padding:16px 12px;margin-bottom:16px;height:48px;background:#fff;box-shadow:0px 0px 3px rgba(0,0,0,.08);border-radius:4px}.task_currentTaskTitle,.completedTask_currentTaskTitle{font-size:14px;line-height:19px;color:#373737}.task_buttons,.completedTask_buttons{display:flex;justify-content:space-between;align-items:center;width:40px;margin-left:15px}.task_completedTaskTitle,.completedTask_completedTaskTitle{font-size:14px;line-height:19px;-webkit-text-decoration-line:line-through;text-decoration-line:line-through;color:#a3a3a3}.task input[type=checkbox],.completedTask input[type=checkbox]{-webkit-appearance:none;width:14px;height:14px;padding:3px;border:1px solid #550dc9;border-radius:4px;margin-right:8px}.task input[type=checkbox]:checked,.completedTask input[type=checkbox]:checked{background-color:#550dc9}.task input[type=checkbox]:hover,.completedTask input[type=checkbox]:hover{cursor:pointer}.completedTask{width:26vw}@media(max-width: 1200px){.App{grid-template-areas:"h h" "n c" "n d";grid-template-rows:64px 1fr 1fr;grid-template-columns:.5fr 10fr}.completedTasks{padding-left:24px}.completedTask{width:64vw}}@media(max-width: 576px){.task,.completedTask{width:87vw}.addItemForm_input{width:77vw}}@media(max-width: 480px){.addItemForm_input{width:68vw}}
